// retire-lah-client/src/components/RetirementChart.jsx
import React from 'react';
import {
  ResponsiveContainer,
  ComposedChart,
  Bar,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ReferenceDot,
  Label,
} from 'recharts';

const CustomCombinedTooltip = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    const capitalInvestedEntry = payload.find(p => p.dataKey === 'capitalInvested');
    const capitalGainsEntry = payload.find(p => p.dataKey === 'capitalGains');
    // totalValue is available directly in the payload of the original data point for the line or bars
    const dataPoint = payload[0]?.payload || {}; // Get the underlying data object for the hovered point

    return (
      <div className="bg-white p-3 shadow-lg rounded-md border border-gray-300 text-sm opacity-95">
        <p className="label font-semibold text-brand-dark-text mb-1.5">{`Age: ${label}`}</p>
        {capitalInvestedEntry && (
          <p style={{ color: capitalInvestedEntry.fill }} className="font-medium">
            {`${capitalInvestedEntry.name}: SGD ${capitalInvestedEntry.value.toLocaleString(undefined, {minimumFractionDigits: 0, maximumFractionDigits: 0})}`}
          </p>
        )}
        {capitalGainsEntry && (
          <p style={{ color: capitalGainsEntry.fill }} className="font-medium">
            {`${capitalGainsEntry.name}: SGD ${capitalGainsEntry.value.toLocaleString(undefined, {minimumFractionDigits: 0, maximumFractionDigits: 0})}`}
          </p>
        )}
        {/* Ensure dataPoint.totalValue exists and is a number before trying to format it */}
        {dataPoint.totalValue !== undefined && typeof dataPoint.totalValue === 'number' && (
             <p className="font-bold mt-2 pt-2 border-t border-gray-200 text-brand-dark-text">
                {`Total Net Worth: SGD ${dataPoint.totalValue.toLocaleString(undefined, {minimumFractionDigits: 0, maximumFractionDigits: 0})}`}
            </p>
        )}
      </div>
    );
  }
  return null;
};

const MONETARY_MILESTONES = [
  { value: 100000, label: '100k Reached!', shortLabel: '100k' },
  { value: 250000, label: '250k Saved!', shortLabel: '250k' },
  { value: 500000, label: 'Half Million!', shortLabel: '500k' },
  { value: 1000000, label: '1M Achieved!', shortLabel: '1 Mil' },
  { value: 1500000, label: '1.5M Milestone', shortLabel: '1.5M' },
];

const RetirementChart = ({ data, targetNestEgg, assumptions }) => {
  if (!data || data.length === 0 || !assumptions) {
    return (
      <div className="bg-brand-card-bg p-6 sm:p-8 rounded-xl shadow-card-lg mt-8 sm:mt-10">
        <h3 className="text-xl sm:text-2xl font-semibold text-brand-dark-text mb-4">Investment Projection</h3>
        <div className="text-center text-brand-medium-text py-16">
          <p className="text-lg">Enter details to see projection.</p>
        </div>
      </div>
    );
  }

  const formatYAxis = (tickItem) => `${tickItem / 1000}k`;
  const lastAgeDataPoint = data[data.length - 1];
  const lastAge = lastAgeDataPoint?.age || 'N/A';
  
  const chartCapitalColor = '#A0AEC0'; 
  const chartGainsColor = '#68D391';   
  const netWorthLineColor = '#3B82F6'; 
  const brandMediumTextColor = '#4B5563'; 
  const brandLightTextColor = '#6B7280'; 

  const achievedMilestones = new Set();
  const milestoneReferenceDots = [];

  data.forEach((point) => {
    MONETARY_MILESTONES.forEach(milestone => {
      if (!achievedMilestones.has(milestone.label) && point.totalValue >= milestone.value) {
        milestoneReferenceDots.push({
          x: point.age,
          y: point.totalValue, 
          labelContent: milestone.shortLabel,
          fill: '#10B981', // brand-secondary (Green)
          key: `ms-${milestone.value}-${point.age}` // More unique key
        });
        achievedMilestones.add(milestone.label);
      }
    });
  });
  
  if (targetNestEgg && isFinite(targetNestEgg)) {
    const fireAchievedPoint = data.find(p => p.totalValue >= targetNestEgg);
    if (fireAchievedPoint && !achievedMilestones.has('FIRE!')) {
        milestoneReferenceDots.push({
            x: fireAchievedPoint.age,
            y: fireAchievedPoint.totalValue, 
            labelContent: 'F.I.R.E!',
            fill: '#F59E0B', // accent-orange
            key: `fire-ms-${fireAchievedPoint.age}` // More unique key
        });
        achievedMilestones.add('FIRE!');
    }
  }
  
  const numDataPoints = data.length;
  let xAxisInterval = 0; 
  if (numDataPoints > 30) { xAxisInterval = Math.floor(numDataPoints / 10); } 
  else if (numDataPoints > 15) { xAxisInterval = Math.floor(numDataPoints / 13); } // Adjusted slightly
  else if (numDataPoints > 8) { xAxisInterval = 1; }
  else { xAxisInterval = "preserveStartEnd"; } // Default for fewer points

  const allYValues = data.map(p => p.totalValue);
  if (targetNestEgg > 0 && isFinite(targetNestEgg)) {
    allYValues.push(targetNestEgg);
  }
  const maxYValue = Math.max(0, ...allYValues);
  const yDomainMax = Math.ceil((maxYValue * 1.15) / 50000) * 50000 + 100000;

  return (
    <div className="bg-brand-card-bg p-4 sm:p-6 rounded-xl shadow-card-lg mt-8 sm:mt-10">
      <h3 className="text-xl sm:text-2xl font-semibold text-brand-dark-text mb-1">Investment Growth Path</h3>
      <p className="text-xs text-brand-light-text mb-6">Projected values from current age until age {lastAge}.</p>
      <ResponsiveContainer width="100%" height={400}>
        <ComposedChart
          data={data} 
          margin={{ top: 35, right: 20, left: 20, bottom: 70 }} // Increased bottom margin
        >
          <CartesianGrid strokeDasharray="3 3" stroke="#e5e7eb"/>
          <XAxis 
            dataKey="age" 
            label={{ 
              value: 'Age', 
              position: 'insideBottom', 
              dy: 55, // Adjusted for more space below legend
              style: {fontSize: '0.875rem', fill: brandMediumTextColor, fontWeight: 500, fontFamily: 'Inter'} 
            }} 
            tick={{fontSize: '0.75rem', fill: brandLightTextColor, fontFamily: 'Inter'}}
            interval={xAxisInterval}
            padding={{ left: 10, right: 10 }}
            height={30} // Give XAxis area some explicit height
          />
          <YAxis 
            tickFormatter={formatYAxis} 
            label={{ value: 'SGD Value', angle: -90, position: 'insideLeft', dx: -15, style: {fontSize: '0.875rem', fill: brandMediumTextColor, fontWeight: 500, fontFamily: 'Inter'} }}
            tick={{fontSize: '0.75rem', fill: brandLightTextColor, fontFamily: 'Inter'}}
            width={75} 
            tickCount={8} 
            domain={[0, yDomainMax]} 
            allowDataOverflow={false}
          />
          <Tooltip content={<CustomCombinedTooltip />} cursor={{ fill: 'rgba(209, 213, 219, 0.4)' }}/>
          <Legend 
            verticalAlign="bottom" 
            align="center"
            iconType="square" 
            iconSize={10}
            wrapperStyle={{
              fontSize: '0.875rem', 
              fontFamily: 'Inter',
              paddingTop: 10, // Space between X-axis ticks and legend
              // The legend will flow naturally after XAxis ticks, then XAxis label will be pushed by dy
            }}
            payload={[
                { value: 'Capital Invested', type: 'square', id: 'ID01', color: chartCapitalColor },
                { value: 'Capital Gains', type: 'square', id: 'ID02', color: chartGainsColor },
                // { value: 'Net Worth Path', type: 'line', id: 'ID03', color: netWorthLineColor },
            ]}
          />
          <Bar dataKey="capitalInvested" stackId="a" name="Capital Invested" fill={chartCapitalColor} radius={[4, 4, 0, 0]} barSize={numDataPoints > 20 ? 20 : undefined} />
          <Bar dataKey="capitalGains" stackId="a" name="Capital Gains" fill={chartGainsColor} radius={[4, 4, 0, 0]} barSize={numDataPoints > 20 ? 20 : undefined} />
          <Line 
            type="monotone" 
            dataKey="totalValue" 
            name="Projected Net Worth" 
            stroke={netWorthLineColor}
            strokeWidth={2.5} 
            dot={numDataPoints <= 20 ? { r: 3, strokeWidth: 1, fill: netWorthLineColor } : false} 
            activeDot={{ r: 5, strokeWidth: 0 }} 
            legendType="none" 
          />
          
          {milestoneReferenceDots.map(dotProps => (
            <ReferenceDot 
              {...dotProps} 
              yAxisId={0} 
              isFront={true} 
              r={6} 
              stroke="#fff" 
              strokeWidth={1.5}
              key={dotProps.key} // Added key from dotProps
            >
              <Label 
                value={dotProps.labelContent} 
                position="top" 
                fill={dotProps.fill === '#F59E0B' ? '#B45309' : '#047857'} 
                fontSize="0.7rem" 
                fontWeight="bold" 
                fontFamily="Inter" 
                dy={-10} 
              />
            </ReferenceDot>
          ))}
        </ComposedChart>
      </ResponsiveContainer>
    </div>
  );
};
export default RetirementChart;